.copyright-list {
  @include list-clear;
  @include flex-row;
  background-color: $footer-background-color;
  color: $dark-blue-color;
  flex-wrap: wrap;
}

.copyright-list a {
  @include a-clear;
}

.copyright-list__item {
  font-family: ("PT Mono", "Arial", monospace);
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  opacity: 0.5;
  text-transform: uppercase;
}

.copyright-list__item:nth-of-type(1) {
  margin-right: 5%;
}

.copyright-list__item:nth-of-type(2) {
  margin-right: 10%;
}

.copyright-list__item:nth-of-type(3) {
  margin-right: 28%;
}

@media (max-width: $tablet-width) {
  .copyright-list {
    justify-content: space-between;
  }

  .copyright-list__item:nth-of-type(1) {
    display: none;
  }

  .copyright-list__item:nth-of-type(2) {
    position: absolute;
    top: 8%;
    left: 42%;
    width: 40%;
  }
}
